@import 'variables.scss';
html {
    scroll-behavior: smooth;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", "Open Sans", Arial, sans-serif;
  }
  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", "Open Sans", Arial, sans-serif;
  }
#main-container {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    .main-content {
        max-width: 1200px;
        width: 100%;
        display: flex;
        .post-container {
            max-width: 800px;
            padding-left: 15px;
            padding-right: 3px;
            box-sizing: border-box;
            float: left;
            #the-post {
                border: 1px solid rgba(0, 0, 0, 0.1);
                color: rgb(44, 47, 52);
                .featured-area .single-image {
                    margin: 0;
                    position: relative;
                    z-index: -1;
                    img {
                        width: 100%;
                        min-height: 300px;
                        object-fit: cover;
                        overflow: hidden;
                    }
                    .photo-description {
                        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", "Open Sans", Arial, sans-serif;
                        position: absolute;
                        bottom: 20px;
                        left: 20px;
                        padding: 3px;
                        box-sizing: border-box;
                        background: rgba(255, 255, 255, 0.7);
                        font-size: 11px;
                        font-weight: 400;
                        background-size: auto;
                    }
                }
                .disclaimer-block {
                    display: flex;
                    margin: 0 15px;
                    padding: 10px 0;
                    padding-top: 6px;
                    border-bottom: 1px solid #e5e5e5;
                    img {
                        width: 25px;
                        height: 32px;
                        margin-right: 10px;
                    }
                    #disclaimer-text {
                        margin: 0;
                        font-size: 11px;
                        color: #b9b9b9;
                        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", "Open Sans", Arial, sans-serif;
                        font-weight: 400;
                    }
                }
                header h1 {
                    padding: 30px 30px 20px;
                    margin-bottom: 15px;      
                    margin-top: 0;  
                    line-height: 1.4;
                }
                @media (max-width:767px) {
                    header h1 {
                        padding: 20px;
                        margin-bottom: 10px;
                    }
                }
                .entry-content {
                    padding: 0 30px 30px;
                    .review-block {
                        span.anchor {
                            position: relative;
                            top: -70px;
                        }
                        padding-bottom: 30px;
                        border-bottom: 5px solid #e5e5e5;
                        margin-bottom: 30px;
                        &:last-child {
                            padding: 0;
                            border: none;
                        }
                        h1,h2, h3 {
                            font-weight: 600;
                            margin-top: 0;
                            color: #2c2f34;
                            margin-bottom: 10px;
                            line-height: 1.4;
                        }
                    }
                    .review-block:last-child {
                        margin-bottom: 0;
                    }
                    p, ul ,ol {
                        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", "Open Sans", Arial, sans-serif;
                        overflow-wrap: break-word;
                        line-height: 26px;
                        font-size: 15px;
                        margin-top: 0;
                        margin-bottom: 25px;
                        a {
                            color: #7c284e;
                            text-decoration: none;
                            &:hover {
                                color: #000;
                            } 
                        }
                    }
                    ul.bottom-list,
                    ol.bottom-list {
                        li {
                            margin-bottom: 5px;
                        }
                    }
                    .robot-photo {
                        display: flex;
                        justify-content: center;
                        margin-bottom: 20px;
                        padding-top: 10px;
                        img {
                            padding-bottom: 8px;
                        }
                    }
                    @media (max-width: 400px) {
                        .robot-photo img {
                            width: 258px;
                        }
                    }
                    .big-photo {
                        margin-bottom: 25px;
                        img {
                            max-width: 720px;
                            width: 100%;
                            height: auto;
                        }
                    }
                    p.video {
                        margin-top: 25px;
                        margin-bottom: 20px;
                        padding-bottom: 20px;
                        padding-top: 56.25%;
                        overflow: hidden;
                        width: 100%;
                        position: relative;
                        display: flex;
                        justify-content: center;
                        iframe {
                            position: absolute;
                            top: 0;
                            left: 0;
                            bottom: 0;
                            right: 0;
                            border: 0;
                            width: 100%;
                            height: 85%;
                            overflow: hidden;
                        }
                    }
                    p.float-image {
                        img {
                            float: right;
                            margin: 0.375em 0 1em 1.75em;
                        }
                    }
                    @media (max-width:767px) {
                        p.float-image {
                            display: flex;
                            justify-content: center;
                            img {
                                float: none;
                                margin: 0;
                            }
                        }
                    }
                    @media (max-width:767px) {
                        p.float-image {
                            img {
                                width: 258px;
                            }
                        }
                    }
                    p.float-left {
                        img {
                            float: left;
                            margin: 0.375em 1.75em 1em 0;
                        }
                    }
                    @media (max-width:767px) {
                        p.float-left {
                            display: flex;
                            justify-content: center;
                            img {
                                float: none;
                                margin: 0;
                            }
                        }
                    }
                    @media (max-width:767px) {
                        p.float-left {
                            img {
                                width: 258px;
                            }
                        }
                    }
                    .titles-bottom {
                        h1 {
                            font-weight: 700;
                        }
                        h1, h2, h3, h4, h5, h6 {
                            margin: 0 0 0.5em;
                        }
                        h2, h3, h4, h5, h6 {
                            font-weight: 600;
                        }
                    }
                }
                @media (max-width:767px) {
                    .entry-content {
                        padding-top: 15px;
                    }
                }
            }
        }
    }
    @media (max-width:991px) {
        .main-content {
            flex-direction: column;
            margin-top: 80px;
            .post-container {
                max-width: 991px;
                padding: 0;
                #the-post .entry-content {
                    .review-block {
                        span.anchor {
                            position: relative;
                            top: -130px;
                        }
                    }
                }
            }
        }
    }
    h1{
        font-size: 32px;
        font-family: 'Montserrat';
    }
    h2{
        font-size: 26px;
        font-family: 'Montserrat';
    }
    h3 {
        font-size: 20px;
        font-family: 'Montserrat';
    }
    h4{
        font-size: 18px;
        font-family: 'Montserrat';
    }
    h5{
        font-size: 16px;
        font-family: 'Montserrat';
    }
    h6{
        font-size: 14px;
        font-family: 'Montserrat';
    }
}
@media (max-width:991px) {
    #main-container {
        margin-top: 15px;
        padding: 0 15px;
    }
}
@media (max-width:767px) {
    #main-container {
        padding: 0;
    }
}
#scroll-up {
    display: none;
    position: fixed;
    bottom: 15px; 
    right: 15px; 
    z-index: 100;
    border: none;
    outline: none;
    background-color:#7c284e;
    color: white; 
    cursor: pointer; 
    padding: 10px 13px 5px;
    font-size: 18px;
    width: 35px;
    height: 35px;
    &::before {
        content: "";
        width: 5px;
        border-top: 1px solid rgba(255,255,255);
        position: absolute;
        left: 40%;
        top: 45%;
        transform: rotate(-45deg);
    }
    &::after {
        content: "";
        width: 5px;
        border-top: 1px solid rgba(255,255,255);
        position: absolute;
        left: 48%;
        top: 45%;
        transform: rotate(45deg);
    }
}
@media (max-width: 767px) {
    #scroll-up {
        bottom: 45px;
        right: 5px;
    }
}
#scroll-up:hover {
    opacity: 0.85;
}